<template>
    <div>
        <nav aria-label="Page navigation example">
            <ul class="pagination">
                <li class="page-item" :class="{disabled: !pagination.has_pre}">
                <a class="page-link" href="#" aria-label="Previous" @click.prevent="getprodcut(pagination.current_page-1)">
                    <span aria-hidden="true">&laquo;</span>
                </a>
                </li>
                <li class="page-item" @click.prevent="getprodcut(page)" :class="{active: pagination.current_page==page}" v-for="page in pagination.total_pages" :key="page">
                    <a class="page-link" href="#">{{page}}</a>
                    </li>
                <li class="page-item" :class="{disabled: !pagination.has_next}" >
                    <a class="page-link" href="#" aria-label="Next" @click.prevent="getprodcut(pagination.current_page+1)">
                        <span aria-hidden="true">&raquo;</span>
                    </a>
                </li>
            </ul>
        </nav>
    </div>
</template>

<script>
export default {
    data() {
        return {
            page:{}
        }
    },
    methods: {
        getprodcut(page){
            this.$emit("page",page);
        }
    },
    props:{
        pagination:{
            type: null
        }
    }
}
</script>


