<template>
    <div>
        <div class="text-right mt-4">
            <button class="btn btn-outline-primary btn-sm" @click="openModal(true)">建立新的產品</button>
        </div>
        <table class="table mt-4">
            <thead>
                <tr>
                    <td width="120">分類</td>
                    <td>產品名稱</td>
                    <td width="120">原價</td>
                    <td width="120">售價</td>
                    <td width="120">是否啟用</td>
                    <td width="80">編輯</td>
                </tr>
            </thead>
            <loading :active.sync="isLoading" style="margin:auto"></loading>
            <tbody>
                <tr v-for="item in products" :key="item.id">
                    <td>{{item.category}}</td>
                    <td>{{item.title}}</td>
                    <td class="text-right">{{item.origin_price | currency }}</td>
                    <td class="text-right">{{item.price | currency }}</td>
                    <td>
                        <span v-if="item.is_enable" class="text-success">啟動</span>
                        <span v-else class="text-success">未啟動</span>
                    </td>
                    <td>
                        <button class="btn btn-outline-primary btn-sm" @click="openModal(false,item)">編輯</button>
                        <button class="btn btn-outline-primary btn-sm" @click="deleteModal(item)">刪除</button>    
                    </td>
                </tr>
            </tbody>
        </table>
        <pageComponent v-on:page="getprodcut" :pagination="pagination"></pageComponent>
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                    <div class="modal-header bg-dark text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                            <span>新增產品</span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="row">
                            <div class="col-sm-4">
                                <div class="form-group">
                                <label for="image">輸入圖片網址</label>
                                <input type="text" class="form-control" v-model="temProduct.imageUrl" id="image"
                                    placeholder="請輸入圖片連結">
                                </div>
                                <div class="form-group">
                                <label for="customFile">或 上傳圖片
                                    <i class="fas fa-spinner fa-spin"></i>
                                </label>
                                <input type="file"  name="file-to-upload" @change="uploadFile" id="customFile" class="form-control"
                                    ref="files">
                                </div>
                                <img src="../../../ajax-loader.gif" alt="" v-if="uploading">
                                <img v-bind:src="temProduct.imageUrl" class="img-fluid" alt="">
                            </div>
                            <div class="col-sm-8">
                                <div class="form-group">
                                    <label for="title">標題</label>
                                    <input type="text" v-model="temProduct.title" class="form-control" id="title"
                                        placeholder="請輸入標題">
                                    </div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6">
                                            <label for="category">分類</label>
                                            <input type="text" v-model="temProduct.category" class="form-control" id="category"
                                            placeholder="請輸入分類">
                                        </div>
                                    <div class="form-group col-md-6">
                                        <label for="price">單位</label>
                                        <input type="unit"  v-model="temProduct.unit" class="form-control" id="unit"
                                        placeholder="請輸入單位">
                                    </div>
                                    </div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6">
                                            <label for="origin_price">原價</label>
                                            <input type="number" v-model="temProduct.origin_price" class="form-control" id="origin_price"
                                            placeholder="請輸入原價">
                                        </div>
                                        <div class="form-group col-md-6">
                                            <label for="price">售價</label>
                                            <input type="number" v-model="temProduct.price" class="form-control" id="price"
                                            placeholder="請輸入售價">
                                        </div>
                                    </div>
                                    <hr>

                                    <div class="form-group">
                                        <label for="description">產品描述</label>
                                        <textarea type="text" v-model="temProduct.description"  class="form-control" id="description"
                                            placeholder="請輸入產品描述"></textarea>
                                    </div>
                                    <div class="form-group">
                                        <label for="content">說明內容</label>
                                        <textarea type="text" v-model="temProduct.content" class="form-control" id="content"
                                            placeholder="請輸入產品說明內容"></textarea>
                                    </div>
                                    <div class="form-group">
                                        <div class="form-check">
                                            <input class="form-check-input" type="checkbox" id="is_enabled" v-model="temProduct.is_enable" >
                                            <label class="form-check-label" for="is_enabled">
                                            是否啟用
                                            </label>
                                        </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                        <button type="button" class="btn btn-primary" @click="updateProduct">確認</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import $ from 'jquery'
import pageComponent from './pagination'

export default {
    data() {
        return {
            products:[],
            temProduct: {},
            isNew: false,
            isLoading: false,
            uploading:false,
            pagination:{},
        }
    },
    methods: {
        getprodcut(page = 1){
            var api = `${process.env.APIPATH}/api/${process.env.CUSTPATH}/admin/products?page=${page}`
            var vm = this
            vm.isLoading = true
            vm.products = ""
            this.$http.get(api).then((response)=>{
                vm.products = response.data.products
                vm.isLoading = false
                console.log(response.data.products)
                vm.pagination = response.data.pagination
            })
        },
        openModal(isNew,item){
            $("#productModal").modal('show')
            if(isNew){
                this.temProduct = {}
                this.isNew = true
            }else{
                this.temProduct = Object.assign({},item)
                this.isNew = false
            }
        },
        updateProduct() {
            let api = `${process.env.APIPATH}/api/${process.env.CUSTPATH}/admin/product`
            let httpModal = 'post'
            var vm = this
            if(!this.isNew){
                api = `${process.env.APIPATH}/api/${process.env.CUSTPATH}/admin/product/${vm.temProduct.id}`
                httpModal = 'put'
            }
            
            this.$http[httpModal](api,{data:vm.temProduct}).then((response)=>{
                if(response.data.success){
                    $("#productModal").modal('hide')
                    this.getprodcut();
                }else{
                    $("#productModal").modal('hide')
                    this.getprodcut();
                    alert("輸入錯誤");
                }
            })
        },
        deleteModal(item){
            var api = `${process.env.APIPATH}/api/${process.env.CUSTPATH}/admin/product/${item.id}`
            var vm = this
            this.$http.delete(api,{data:vm.deleteItem}).then((response)=>{
                // vm.products = response.data.products
                    alert(response.data.message)
                    vm.getprodcut();
            })
        },
        uploadFile(){
            const uploadedFile = this.$refs.files.files[0]
            const formData = new FormData();
            formData.append('file-to-upload',uploadedFile)
            const vm = this
            vm.uploading= true
            vm.temProduct.imageUrl = ""
            const api = `${process.env.APIPATH}/api/${process.env.CUSTPATH}/admin/upload`
            this.$http.post(api,formData,{
                header:{
                    'Content-Type':'multipart/form-data'
                }
            }).then((response)=>{
                if(response.data.success){
                    vm.uploading= false
                    vm.$set(vm.temProduct,'imageUrl',response.data.imageUrl)
                }else{
                    this.$bus.$emit('messsage:push',response.data.message,"danger");
                    vm.uploading= false
                }
            })
        }
    },
    created() {
        this.getprodcut()
    },
    components:{
        pageComponent
    }
}
</script>


