<template>
    <div>
        <loading :active.sync="isLoading" style="margin:auto"></loading>
        <div class="row mt-4">
            <div class="col-md-4 mb-4" v-for="item in products" :key="item.id">
                <div class="card border-0 shadow-sm">
                    <div style="height: 150px; background-size: cover; background-position: center" :style="{backgroundImage: `url(${item.imageUrl})`}"></div>
                    <div class="card-body">
                    <span class="badge badge-secondary float-right ml-2">{{item.category}}</span>
                    <h5 class="card-title">
                        <a href="#" class="text-dark">{{item.title}}</a>
                    </h5>
                    <p class="card-text">{{item.content}}</p>
                    <div class="d-flex justify-content-between align-items-baseline">
                        <div class="h5" v-if="item.origin_price == item.price">{{item.origin_price|currency}} 元</div>
                        <del class="h6" v-if="item.origin_price !== item.price">原價 {{item.origin_price|currency}} 元</del>
                        <div class="h5" v-if="item.origin_price !== item.price">現在只要 {{item.price|currency}} 元</div>
                    </div>
                    </div>
                    <div class="card-footer d-flex">
                    <button type="button" class="btn btn-outline-secondary btn-sm" @click="getPtoduct(item.id)">
                        <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
                        查看更多
                    </button>
                    <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click="addCart(item.id)">
                        <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
                        加到購物車
                    </button>
                    </div>
                </div>
            </div>
        </div>

        <div class="mt-5 mb-5">
            <table class="table">
                <thead>
                    <th></th>
                    <th>品名</th>
                    <th>數量</th>
                    <th>單價</th>
                </thead>
                <tbody>
                    <tr v-for="item in cart.carts" :key="item.id">
                        <td class="align-middle">
                            <button type="button" class="btn btn-outline-danger btn-sm" @click="removeCartItem(item.id)">
                                <i class="far fa-trash-alt"></i>
                            </button>
                        </td>
                        <td class="align-middle">
                            {{ item.product.title }}
                            <div class="text-success" v-if="item.coupon">
                            已套用優惠券
                            </div>
                        </td>
                        <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                        <td class="align-middle text-right">{{ item.final_total }}</td>
                    </tr>
                </tbody>
                <tfoot>
                    <tr>
                        <td colspan="3" class="text-right">總計</td>
                        <td class="text-right">{{ cart.total }}</td>
                    </tr>
                    <tr v-if="cart.total !== cart.final_total">
                        <td colspan="3" class="text-right text-success">折扣價</td>
                        <td class="text-right text-success">{{ cart.final_total }}</td>
                    </tr>
                </tfoot>
            </table>
            <div class="input-group mb-3 input-group-sm">
                <input type="text" class="form-control" v-model="coupon.coupon_code" placeholder="請輸入優惠碼">
                <div class="input-group-append">
                    <button class="btn btn-outline-secondary" type="button" @click="useCoupon">
                        <i class="fas fa-spinner fa-spin" v-if="coupon.loading"></i>
                        套用優惠碼
                    </button>
                </div>
            </div>
        </div>

        <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel">{{ product.title }}</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <img :src="product.imageUrl" class="img-fluid" alt="">
                        <blockquote class="blockquote mt-3">
                            <p class="mb-0">{{ product.content }}</p>
                            <hr>
                            <footer class="blockquote-footer text-right">{{ product.description }}</footer>
                        </blockquote>
                        <div class="d-flex justify-content-between align-items-baseline">
                            <div class="h4" v-if="product.origin_price==product.origin_price">{{ product.origin_price }} 元</div>
                            <del class="h6" v-if="product.origin_price!==product.origin_price">原價 {{ product.origin_price }} 元</del>
                            <div class="h4" v-if="product.origin_price!==product.origin_price">現在只要 {{ product.price }} 元</div>
                        </div>
                        <select name="" class="form-control mt-3" v-model="product.num">
                            <option :value="num" v-for="num in 10" :key="num">選購 {{num}} {{product.unit}}</option>
                        </select>
                    </div>
                    <div class="modal-footer">
                        <div class="text-muted text-nowrap mr-3">
                            小計 <strong>{{ product.num * product.price }}</strong> 元
                        </div>
                        <button type="button" class="btn btn-primary" @click="addtoCart(product.id, product.num)">
                            <i class="fas fa-spinner fa-spin" v-if="product.id === status.loadingItem"></i>
                            加到購物車
                        </button>
                    </div>
                </div>
            </div>
        </div>

        <div class="my-5 row justify-content-center">
            <form class="col-md-6" @submit.prevent="addOrder">
                <div class="form-group">
                    <label for="useremail">Email</label>
                    <input type="email" class="form-control" name="email" id="useremail"
                    v-validate="'required|email'"
                        v-model="form.user.email" placeholder="請輸入 Email" required>
                    <span class="text-danger">{{ errors.first('email') }}</span>
                </div>
            
                <div class="form-group">
                    <label for="username">收件人姓名</label>
                    <input type="text" class="form-control" :class="{'is-invalid':errors.has('name')}" name="name" v-validate="'required'" id="username"
                        v-model="form.user.name" placeholder="輸入姓名">
                    <span class="text-danger" v-if="errors.has('name')">姓名為必填</span>
                </div>
            
                <div class="form-group">
                    <label for="usertel">收件人電話</label>
                    <input name="tel" type="tel" class="form-control" v-validate="'required'" id="usertel" v-model="form.user.tel" placeholder="請輸入電話">
                    <span class="text-danger" v-if="errors.has('tel')">電話欄位不得留空</span>
                </div>
            
                <div class="form-group">
                    <label for="useraddress">收件人地址</label>
                    <input type="text" class="form-control" name="address" id="useraddress" v-model="form.user.address"
                    placeholder="請輸入地址" v-validate="'required'">
                    <span class="text-danger" v-if="errors.has('address')">地址欄位不得留空</span>
                </div>
            
                <div class="form-group">
                <label for="comment">留言</label>
                <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
                </div>
                <div class="text-right">
                    <button class="btn btn-danger" >送出訂單</button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import $ from 'jquery'

export default {
    data(){
        return {
            products:[],
            product:{},
            isLoading:false, 
            status: {
                loadingItem: "",
            },
            cart:{
                carts: [],
                total:'',
                final_total:''
            },
            coupon:{
                coupon_code:"",
                loading:false
            },
            form:{
                user:{
                    email:"",
                    name:"",
                    tel:"",
                    address:"",
                },
                message:""
            }
        }
    },
    methods:{
        getPtoducts(){
            const vm = this
            vm.isLoading= true
            const api = `${process.env.APIPATH}/api/${process.env.CUSTPATH}/products/all`
            this.$http.get(api).then(Response=>{
                vm.products = Response.data.products
                vm.isLoading= false
            })
        },
        getPtoduct(id){
            const vm = this
            vm.status.loadingItem= id
            const api = `${process.env.APIPATH}/api/${process.env.CUSTPATH}/product/${id}`
            this.$http.get(api).then(Response=>{
                $("#productModal").modal('show');
                vm.product = Response.data.product
                vm.status.loadingItem = ""
            })
        },
        addCart(id , qty=1){
            const vm = this
            vm.status.loadingItem= id
            const cart ={
                product_id: id,
                qty:qty
            } 
            const api = `${process.env.APIPATH}/api/${process.env.CUSTPATH}/cart`
            this.$http.post(api,{data:cart}).then(Response=>{
                if(Response.data.success){
                    $("#productModal").modal('hide');
                    vm.status.loadingItem = "";
                    vm.getCart();
                }else{
                    this.$bus.$emit("message:push",Response.data.message,"danger")
                }
            })
        },
        getCart(){
            const vm = this
            vm.isLoading= true
            const api = `${process.env.APIPATH}/api/${process.env.CUSTPATH}/cart`
            this.$http.get(api).then(Response=>{
                vm.cart.carts = Response.data.data.carts
                vm.cart.total = Response.data.data.total
                vm.cart.final_total = Response.data.data.final_total
                vm.isLoading= false
            })
        },
        removeCartItem(id){
            const vm = this
            vm.isLoading= true
            const api = `${process.env.APIPATH}/api/${process.env.CUSTPATH}/cart/${id}`
            this.$http.delete(api).then(Response=>{
                vm.getCart();
                vm.isLoading= false
            })
        },
        useCoupon(){
            const vm = this
            vm.coupon.loading = true
            const coupon = {
                "code": vm.coupon.coupon_code
            }
            const api = `${process.env.APIPATH}/api/${process.env.CUSTPATH}/coupon`
            this.$http.post(api,{data:coupon}).then(Response=>{
                vm.coupon.loading= false
                vm.getCart();
            })
        },
        async addOrder(){
            const vm = this
            const order = {
                user: vm.form.user,
                message: vm.form.message
            }
            const api = `${process.env.APIPATH}/api/${process.env.CUSTPATH}/order`

            this.$validator.validate().then(result => {
                if(result){
                    vm.$http.post(api,{data:order}).then(Response=>{
                        if(Response.data.success){
                            vm.$router.push(`/orderCheckOut/${Response.data.orderId}`)
                        }
                        else{
                            vm.$bus.$emit("message:push","訂單建立失敗","alert");
                        }
                    })
                }
                else{
                    vm.$bus.$emit("message:push","輸入錯誤","alert");
                }
            })
        }
    },
    created() {
        this.getPtoducts();
        this.getCart()
    },
}
</script>


