<template>
    <div>
        <table class="table mt-4">
            <thead>
                <tr>
                    <th class="text-center" width="80">編號</th>
                    <th>訂單編號</th>
                    <th>訂貨人</th>
                    <th>訂單日期</th>
                    <th width="80">總金額</th>
                    <th width="100">是否已結帳</th>
                </tr>
            </thead>
            <loading :active.sync="isLoading" style="margin:auto"></loading>
            <tbody v-if="!isLoading">
                <tr v-for="item in order" :key="item.num">
                    <td class="text-center">{{item.num}}</td>
                    <td><a href="javascript:void(0)" @click="openDeatil(item)">{{item.id}}</a></td>
                    <td>{{name(item)}}</td>
                    <td>{{item.create_at}}</td>
                    <td class="text-right">{{item.total}}</td>
                    <td class="text-right text-success">
                        <span v-if="item.is_paid">是</span>
                        <span v-else>否</span>
                    </td>
                </tr>
            </tbody>
        </table>
        <pageComponent v-on:page="getOrder" :pagination="pagination"></pageComponent>
        <div class="modal fade" id="detailModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                    <div class="modal-header bg-dark text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                            <span>新增產品</span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="row">
                            <div class="col-sm-8">
                                <div class="form-group">
                                    <label for="title">標題</label>
                                    <!-- <input type="text" v-model="detail.title" class="form-control" id="title"
                                        placeholder="請輸入標題"> -->
                                </div>
                                <div class="form-row">
                                    <div class="form-group col-md-6">
                                        <label for="category">分類</label>
                                        <!-- <input type="text" v-model="detail.category" class="form-control" id="category"
                                        placeholder="請輸入分類"> -->
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="price">單位</label>
                                        <!-- <input type="unit"  v-model="detail.unit" class="form-control" id="unit"
                                        placeholder="請輸入單位"> -->
                                    </div>
                                </div>
                                <div class="form-row">
                                    <div class="form-group col-md-6">
                                        <label for="origin_price">原價</label>
                                        <!-- <input type="number" v-model="detail.origin_price" class="form-control" id="origin_price"
                                        placeholder="請輸入原價"> -->
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="price">售價</label>
                                        <!-- <input type="number" v-model="detail.price" class="form-control" id="price"
                                        placeholder="請輸入售價"> -->
                                    </div>
                                </div>
                                <hr>
                                <div class="form-row">
                                    <div class="form-group">
                                        <label for="description">產品描述</label>
                                        <!-- <textarea type="text" v-model="detail.description"  class="form-control" id="description"
                                            placeholder="請輸入產品描述"></textarea> -->
                                    </div>
                                    <div class="form-group">
                                        <label for="content">說明內容</label>
                                        <!-- <textarea type="text" v-model="detail.content" class="form-control" id="content"
                                            placeholder="請輸入產品說明內容"></textarea> -->
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">關閉</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import $ from 'jquery'
import pageComponent from './pagination'

export default {
    data(){
        return{
            order:{
                user:{}
            },
            pagination:{},
            isLoading: false,
            detail:{}
        }
    },
    methods:{
        getOrder(page = 1){
            const vm = this
            const api = `${process.env.APIPATH}/api/${process.env.CUSTPATH}/admin/orders?${page}`
            this.$http.get(api).then(Response=>{
                vm.order = Response.data.orders
                vm.pagination = Response.data.pagination
            });
        },
        name(item){
            if(item.user == undefined){
                return "未命名"
            }else{
                const user = JSON.parse(JSON.stringify(item.user))
                return user.name
            }
        },
        openDeatil(item){
            $("#detailModal").modal("show")
        }
    },
    created(){
        this.getOrder();
    },
    components:{
        pageComponent
    }
}
</script>


