<template>
    <div>
        <div class="text-right mt-4">
            <button class="btn btn-outline-primary btn-sm" @click.prevent="openModal(true)">建立新的優惠卷</button>
        </div>

        <table class="table mt-4">
            <thead>
                <tr>
                    <td>標題</td>
                    <td width="80">折扣%</td>
                    <td width="120">到期日</td>
                    <td width="80">代碼</td>
                    <td width="100">是否啟用</td>
                    <td width="80">編輯</td>
                </tr>
            </thead>
            <loading :active.sync="isLoading" style="margin:auto"></loading>
            <tbody v-if="!isLoading">
                <tr v-for="item in coupon" :key="item.id">
                    <td>{{item.title}}</td>
                    <td>{{item.percent}}</td>
                    <td>{{item.due_date }}</td>
                    <td>{{item.code}}</td>
                    <td>
                        <span v-if="item.is_enable" class="text-success">啟動</span>
                        <span v-else class="text-success">未啟動</span>
                    </td>
                    <td>
                        <button class="btn btn-outline-primary btn-sm" @click="openModal(false,item)">編輯</button>
                        <button class="btn btn-outline-primary btn-sm" @click="deleteCoupon(item.id)">刪除</button>    
                    </td>
                </tr>
            </tbody>
        </table>

        <!-- <pageComponent v-on:page="getprodcut" :pagination="pagination"></pageComponent> -->

        <div class="modal fade" id="couponModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                    <div class="modal-header bg-dark text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                            <span>新增產品</span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>

                    <div class="modal-body">
                        <div class="row">
                            <div class="col-sm-8">
                                <div class="form-group">
                                    <label for="title">標題</label>
                                    <input type="text" v-model="newCoupon.title" class="form-control" id="title"
                                        placeholder="請輸入標題">
                                </div>
                                <div class="form-group">
                                    <label for="title">折扣%</label>
                                    <input type="number" v-model="newCoupon.percent" class="form-control" id="title"
                                        placeholder="請輸入數字 最多到100" max="100" min="0">
                                </div>
                                <div class="form-group">
                                    <label for="title">到期日</label>
                                    <input type="date" @change="getDate" class="form-control" id="title"
                                        placeholder="請輸入到期日">
                                </div>
                                <div class="form-group">
                                    <label for="title">代碼</label>
                                    <input type="text" v-model="newCoupon.code" class="form-control" id="title"
                                        placeholder="請輸入代碼">
                                </div>
                                <hr>

                                <div class="form-group">
                                    <div class="form-check">
                                        <input class="form-check-input" type="checkbox" id="is_enabled" v-model="newCoupon.is_enable" >
                                        <label class="form-check-label" for="is_enabled">
                                        是否啟用
                                        </label>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                        <button type="button" class="btn btn-primary" @click="addCoupon(newCoupon.id)">確認</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import $ from 'jquery'
import pageComponent from './pagination'

export default {
    data(){
        return{
            coupon:[],
            page: "1",
            isNew: false,
            isLoading:false,
            newCoupon:{
                title: "",
                percent: 100,
                due_date: "",
                code: "",
                is_enabled: false,
            },
        }
    },
    methods:{
        openModal(isNew,item){
            $("#couponModal").modal('show');
            if(isNew){
                this.newCoupon={}
                this.isNew = true
            }else{
                this.newCoupon=item
                this.isNew = false
            }
            
        },
        addCoupon(id){
            const vm = this
            vm.isLoading = true
            const coupon = this.newCoupon
            
            if(this.isNew){
                var api = `${process.env.APIPATH}/api/${process.env.CUSTPATH}/admin/coupon`
                var htttpItem = "post"
            }else{
                var api = `${process.env.APIPATH}/api/${process.env.CUSTPATH}/admin/coupon/${id}`
                var htttpItem = "put"
            }
            this.$http[htttpItem](api,{data:coupon}).then(Response=>{
                $("#couponModal").modal('hide');
                vm.isLoading = false
                vm.getCoupon();
            })
        },
        getCoupon(page = 1){
            const vm = this
            vm.isLoading = true
            const api = `${process.env.APIPATH}/api/${process.env.CUSTPATH}/admin/coupons?page=${page}`
            this.$http.get(api).then(Response=>{
                vm.coupon = Response.data.coupons
                vm.coupon.forEach((item)=>{
                    var year = new Date(item.due_date*1000).getFullYear();
                    var month = new Date(item.due_date*1000).getMonth() + 1;
                    var day = new Date(item.due_date*1000).getDate();
                    var date = year +"-"+month+"-"+day
                    item.due_date = date
                })
                vm.isLoading = false
            })
        },
        deleteCoupon(id){
            const vm = this
            vm.isLoading = true
            const api = `${process.env.APIPATH}/api/${process.env.CUSTPATH}/admin/coupon/${id}`
            this.$http.delete(api).then(Response=>{
                vm.isLoading = false
                vm.getCoupon();
            })
        },
        getDate(e){
            this.newCoupon.due_date = new Date(e.currentTarget.value).getTime()/1000
        }
    },
    created() {
        this.getCoupon();
    },
}
</script>


