<template>
    <div>
        <form class="form-signin" @submit.prevent="signin">
            <h1 class="h3 mb-3 font-weight-normal">Please sign in</h1>
            <label for="inputEmail" class="sr-only">Email address</label>

            <input type="email" id="inputEmail" v-model="user.username" class="form-control" placeholder="Email address" required autofocus>
            
            <label for="inputPassword" class="sr-only">Password</label>
            
            <input type="password" id="inputPassword"  v-model="user.password" class="form-control" placeholder="Password" required>
            
            <div class="checkbox mb-3">
                <label>
                <input type="checkbox" value="remember-me"> Remember me
                </label>
            </div>
            <button class="btn btn-lg btn-primary btn-block" type="submit">Sign in</button>
            <p class="mt-5 mb-3 text-muted">&copy; 2017-2019</p>
        </form>
        <router-link to="/customer_orders">返回產品頁</router-link>
    </div>
</template>

<script>
export default {
    name: 'HelloWorld',
    data() {
        return {
            user: {
                username: '',
                password: ''
            },
        }
    },
    methods: {
        signin(){
            var api = `${process.env.APIPATH}/admin/signin`;
            var vm = this
            this.$http.post(api,vm.user).then((response)=>{
                if(response.data.success){
                    this.$router.push('/admin/product')
                }else{
                    alert("輸入錯誤")
                }
            })
            
        }
    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



